import  React,{useContext} from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import logo from "../assets/logo.png";
import Container from "@mui/material/Container";
import { Link, useLocation } from "react-router-dom";
import useMediaQuery from "@mui/material/useMediaQuery";
import { appbrdiv } from "./style";
import { AppContext } from "../context/createContext";


const drawerWidth = 240;

const NavBars = () => {
  const location = useLocation();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const matches = useMediaQuery("(max-width:600px)");
  const {setData} = useContext(AppContext)

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };
  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: "center" }}>
      <Box style={{ width: "160px",margin:matches ? 'auto':'0px' }}>
        <Link to="/">
          <img src={logo} alt="" style={{ width: "100px" }} />
        </Link>
      </Box>
      <Divider />
      <List>
        <Link
          to="/"
          className={location.pathname === "/" ? "active" : "linktag"}>
          <ListItem disablePadding>
            <ListItemButton sx={{ textAlign: "center" }}>
              <ListItemText primary="Home" />
            </ListItemButton>
          </ListItem>
        </Link>
        <Link
          to="/Calculators"
          className={location.pathname === "/Calculators" ? "active" : "linktag"}>
          <ListItem disablePadding>
            <ListItemButton sx={{ textAlign: "center" }}>
              <ListItemText primary="Calculators" />
            </ListItemButton>
          </ListItem>
        </Link>
        <Link
          to="/Contact"
          className={location.pathname === "/Contact" ? "active" : "linktag"} onClick={()=>setData("Contact Us")}>
          <ListItem disablePadding>
            <ListItemButton sx={{ textAlign: "center" }}>
              <ListItemText primary="Contact" />
            </ListItemButton>
          </ListItem>
        </Link>
      </List>
    </Box>
  );

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar component="nav" style={appbrdiv}>
        <Container maxWidth="xl" style={{ padding: "0px" }}>
          <Toolbar>
            <Box
              style={{
                display: matches ? "flex" : "none",
                justifyContent: "space-between",
                alignItems: "center",
                width: "100%",
              }}>
                <Box style={{ width: "160px"}}>
              <Link to="/">
                  <img src={logo} alt="" style={{ width: "100px" }} />
              </Link>
                </Box>
              <IconButton
                color="inherit"
                aria-label="open drawer"
                edge="start"
                onClick={handleDrawerToggle}
                sx={{
                  mr: 2,
                }}
              >
                <MenuIcon />
              </IconButton>
            </Box>
            <Typography
              variant="h6"
              component="div"
              sx={{ flexGrow: 1, display: { xs: "none", sm: "block" } }}>
            <Box style={{ width: "190px",marginTop:'10px'}}>
            <Link to='/' >
                <img src={logo} alt="" style={{ width: "100%" }} />
            </Link>
              </Box>
            </Typography>
            <Box sx={{ display: { xs: "none", sm: "flex", gap: 15 } }}>
              <Link
                to="/"
                className={location.pathname === "/" ? "active" : "linktag"}>
                Home
              </Link>
              <Link
                to="/Calculators"
                className={location.pathname === "/Calculators" ? "active" : "linktag"}>
                Calculators
              </Link>
              <Link
                to="/Contact"
                className={location.pathname === "/Contact" ? "active" : "linktag"}onClick={()=>setData("Contact Us")}>
                Contact 
              </Link>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
      <nav>
        <Drawer
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true,
          }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
            },
          }}
        >
          {drawer}
        </Drawer>
      </nav>
    </Box>
  );
};

export default NavBars;
