import { createContext } from "react";
import { useState } from "react";

export const AppContext = createContext({});
export const AppContextProvider = ({ children }) => {

  const[interestOnly, setInterestOnly] = useState(0);
  const [totleYears, setTotleYears] = useState(null);
  const[totleLoan, setTotleLoan] = useState(null);
  const[interestchrg, setInterestchrg] = useState(null);
  const[interestRate, setInterestRates] = useState(null);
  const[loanAmount, setLoanAmounts] = useState(null);
  const [secsuse, setSecsuse] = useState(null);
  const [data, setData] = useState("");
  const[paths ,setPaths] = useState('');
 
  return (
    <AppContext.Provider
      value={{
        data,
        setData,
        totleYears,
        setTotleYears,
        totleLoan, 
        setTotleLoan,
        interestchrg,
        setInterestchrg,
        interestRate,
        setInterestRates,
        loanAmount,
        setLoanAmounts,
        interestOnly,
        setInterestOnly,
        secsuse,
        setSecsuse,
        paths,
        setPaths
      }}
    >
      {children}
    </AppContext.Provider>
  );
};
