import React, { useContext, useEffect } from "react";
import { BrowserRouter, Routes, Route, useLocation } from "react-router-dom";
import Home from "./pages/Home";
import Calculators from "./pages/Calculators";
import Contect from "./pages/Contect";
import Footer from "./components/Footer";
import NavBars from "./components/NavBars";
import PrivacyPolicy from "./pages/PrivacyPolicy";
import TermsandConditions from "./pages/TermsandConditions";
import Admin from "./pages/Admin";
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import ChartCpnt from "./components/ChartCpnt";
import { AppContext } from "./context/createContext";

function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

function App() {
  const { paths } = useContext(AppContext);
  console.log(paths);

  // Assume some criteria to determine if the user is an admin
  const isAdmin = true; // Replace with your actual admin check logic

  return (
    <div className="App">
      <BrowserRouter>
        <ScrollToTop />
        <NavBars />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/Calculators" element={<Calculators />} />
          <Route path="/Contact" element={<Contect />} />
          <Route path="/PrivacyPolicy" element={<PrivacyPolicy />} />
          <Route path="/TermsandConditions" element={<TermsandConditions />} />
          <Route path="/ChartCpnt" element={<ChartCpnt />} />
          {isAdmin && <Route path="/admin" element={<Admin />} />}
        </Routes>
        {paths === "/" ? null : <Footer />}
        <ToastContainer />
      </BrowserRouter>
    </div>
  );
}

export default App;
