import React, { useContext, useEffect, useRef } from "react";
import { Box, Container, Button, Typography } from "@mui/material";
import {
  heroBTN,
  cardsH,
  cardh1,
  cardsmain,
} from "../components/style";
import { cardData1 } from "../assets/data";
import useMediaQuery from '@mui/material/useMediaQuery';
import { Link } from "react-router-dom";
import { AppContext } from "../context/createContext";

const Cards = () => {
  const { setData } = useContext(AppContext);
  const matches = useMediaQuery('(max-width:900px)');
  const match40 = useMediaQuery('(max-width:520px)');
  const match12 = useMediaQuery('(max-width:1200px)');
  const cardsRef = useRef(null);

  useEffect(() => {
  if(!match12){
    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.style.transition = "opacity 2s, transform 2s"; 
          entry.target.style.opacity = 1;
          entry.target.style.transform = "translateY(0)";
        } else {
          entry.target.style.transition = "opacity 2s, transform 2s";
          entry.target.style.opacity = 0;
          entry.target.style.transform = "translateY(30%)";
        }
      });
    });

    const cards = cardsRef.current.querySelectorAll(".anima");
    cards.forEach((card) => {
      observer.observe(card);
    });
    return () => {
      observer.disconnect();
    };
  }
  }, [match12]);

  return (
    <>
      <div className={match12 ? "":"scroldiv"} ref={match12 ? null:cardsRef}>
       <Box className={match12 ? 'scroldiv':"anima"}>
       <Container maxWidth="xl">
          <Box style={cardsmain} >
            {cardData1.map((item, i) => (
              <Box
                className={`fistBox`}
                key={i}
                style={{ ...cardsH, width: match40 ? '90%' : matches ? '100%' : '400px' }}
              >
                <Link
                  to='/Contact'
                  style={{ textDecoration: 'none', color: 'unset' }}
                  onClick={() => setData(item)}
                >
                  <Box className='boxaction'>
                    <Box>
                      <img src={item.icon} alt="" width={50} />
                    </Box>
                    <Typography variant="body1" style={cardh1}>
                      {item.title}
                    </Typography>
                    <Typography variant="body2" className="desc">
                      {item.desc}
                    </Typography>
                  </Box>
                </Link>
              </Box>
            ))}
          </Box>
        </Container>
        <Box style={{ width: "fit-content", margin: "2rem auto 0px" }}>
          <Link to='/Contact' onClick={() => setData("Contact Us")}>
            <Button variant="contained" className="btncontect" style={heroBTN}>
              Contact Us
            </Button>
          </Link>
        </Box>
       </Box>
      </div>
    </>
  );
};

export default Cards;
