import React, { useContext, useState,useEffect } from 'react'
import { Container, Box, Button, Typography } from '@mui/material'
import { formCont, conInput, textaracon, heroBTN, contdiv, contecmndev } from '../components/style'
import { Link, useLocation } from 'react-router-dom'
import useMediaQuery from '@mui/material/useMediaQuery';
import gfi2 from '../assets/2.gif'
import { AppContext } from '../context/createContext'
import axios from 'axios'
import { useSnackbar } from "notistack";
import Loader from '../components/Loader';


const Contect = () => {
  
  const uselocation = useLocation()
  const[loading ,setLoading] = useState(false)
  const { enqueueSnackbar } = useSnackbar();
  const { data,setPaths } = useContext(AppContext)
  const match50 = useMediaQuery('(max-width:800px)');
  const [username, setUsername] = useState()
  const [email, setemail] = useState()
  const [phone, setPhone] = useState()
  const [message, setMessage] = useState()
  const [isChecked, setIsChecked] = useState(false);
  const[errms ,setErrms] = useState("")

useEffect(()=>{
  setPaths(uselocation.pathname)
},[uselocation])



  const handleCheckboxChange = (event) => {
    setIsChecked(event.target.checked);
  };

  const handelsubmit = async () => {
    setLoading(true)
    if(!username || !email || !phone || !message) {
      enqueueSnackbar("Please provide all fields", { variant: "error" });
      setLoading(false)
    }
    const formdata = {
      username,
      email,
      phone,
      message
    }
    console.log(formdata)
    try {
      if(!isChecked) return setErrms("make sure Terms And Privacy Policy"),setLoading(false)
      const data = await axios.post('http://localhost:8000/api/form/userdata', formdata)
      console.log(data)
      enqueueSnackbar(data.data, { variant: "success" });
      setUsername("")
      setemail("")
      setPhone("")
      setMessage("")
      setErrms("")
      setLoading(false)
    } catch (error) {
      console.log(error)
    }

  }


  return (
    <>
      <div>
       {loading  && <Loader/>}
        <div className='contectmain'> </div>
        <Container maxWidth="xl">
          <Box style={contdiv}>
            <Box style={{ margin: '1rem auto' }}>
              <h1 style={{ color: 'white', textAlign: 'center' }}>{data.title ? data.title : data}</h1>
              <Box style={{ ...contecmndev, flexDirection: match50 ? 'column' : 'row' }}>
                <form action="/" style={{ ...formCont, width: match50 ? '100%' : '50%', padding: match50 ? '2rem 1rem' : '2rem' }}>
                  <Box>
                    <label htmlFor="1">Name</label><br />
                    <input type="text" name="name" id='1' value={username} placeholder="Enter Your Name" style={conInput} onChange={(e) => setUsername(e.target.value)} />
                  </Box>
                  <Box>
                    <label htmlFor="2">Contact Number</label><br />
                    <input type="number" name="number" id='2' value={phone} placeholder="Enter Your Contact Number" style={conInput} onChange={(e) => setPhone(e.target.value)}  className="hide-number-arrow"/>
                  </Box>
                  <Box>
                    <label htmlFor="3">Email</label><br />
                    <input type="email" name="Email" required id='3' value={email} placeholder="Enter Your Email Address" style={conInput} onChange={(e) => setemail(e.target.value)} />
                  </Box>
                  <Box>
                    <label>Enquiry</label><br />
                    <textarea name="textarea" value={message} placeholder='Type Your Enquiry' onChange={(e) => setMessage(e.target.value)} style={textaracon}></textarea>
                    <Box style={{ display: 'flex', alignItems: 'center', gap: '5px' }}>
                      <input 
                      type="checkbox"
                      id="checkbox"
                      name="checkbox"
                      checked={isChecked}
                      onChange={handleCheckboxChange}></input>
                      <label htmlFor="checkbox" style={{ marginBottom: 0, fontSize: 14 }}>I accept <Link to='/TermsandConditions' style={{ color: '#5C5CFF' }}> Terms and Conditions</Link> and <Link style={{ color: '#5C5CFF' }}
                        to='/PrivacyPolicy'>Privacy Policy</Link> 
                        </label>
                    </Box>
                       <Typography variant="caption" color="error">{errms ? errms:null}</Typography>
                  </Box>
                  <Box>
                    <Button variant="contained" className='btncontect' onClick={handelsubmit} style={heroBTN}>submit</Button>
                  </Box>
                </form>
                <Box style={{ width: match50 ? '100%' : '40%' }}>
                  <img src={gfi2} alt="" style={{ width: '90%' }} />
                </Box>
              </Box>
            </Box>
          </Box>
        </Container>
      </div>

    </>
  )
}

export default Contect