import React, { useContext, useRef, useEffect } from "react";
import { Box, Container, Button, Typography } from "@mui/material";
import {
    heroH1,
    heroBTN,
    arwimg,
} from "../components/style";
import useMediaQuery from '@mui/material/useMediaQuery';
import ghf1 from '../assets/1.gif'
import Chavron from "../components/Chavron";
import { Link } from "react-router-dom";
import { AppContext } from "../context/createContext";

const HeroSection = () => {
    const { setData } = useContext(AppContext);
    const matches = useMediaQuery('(max-width:900px)');
    const match12 = useMediaQuery('(max-width:1200px)');
    const match40 = useMediaQuery('(max-width:520px)');
    const heroRef = useRef(null);

    useEffect(() => {
        if (!match12) {
          const observer = new IntersectionObserver((entries) => {
            entries.forEach((entry) => {
              if (entry.isIntersecting) {
                entry.target.style.transition = "opacity 2s, transform 2s"; 
                entry.target.style.opacity = 1;
                entry.target.style.transform = "translateY(0)";
              } else {
                entry.target.style.transition = "opacity 2s, transform 2s"; 
                entry.target.style.opacity = 0;
                entry.target.style.transform = "translateY(20%)";
              }
            });
          }, {
            root: null,
            rootMargin: '0px',
            threshold: 0.5,
          });
      
          const cards = heroRef.current.querySelectorAll(".anima");
          cards.forEach((card) => {
            observer.observe(card);
          });
      
          return () => {
            observer.disconnect();
          };
        }
      }, [match12]);
      
      
    return (
        <>
            <div className={match12 ? "":"scroldiv"} ref={match12 ? null: heroRef}>
             <Box className={match12 ? '':"anima"}>
             <Container maxWidth="xl">
                    <Box className="mainhero">
                        <Box style={{ width: matches ? '100%' : "45%", textAlign: match40 ? 'center' : 'unset' }}>
                            <h1 className="animated-text" style={{ ...heroH1, fontSize: match40 ? '30px' : '60px', lineHeight: match40 ? 'unset' : '60px' }}>
                                Save On Your <br /> Loan Today
                            </h1>
                            <p style={{ fontSize: "18px" }}>
                                We're your trusted partner in securing the right finance solution, <br /> guiding you every step of the way. Reach out to us today for a complimentary review
                            </p>
                            <Link to='/Contact' onClick={() => setData("Contact Us")}>
                                <Button variant="contained" className="btncontect" style={heroBTN}>
                                    Contact Us
                                </Button>
                            </Link>
                        </Box>
                        <Box style={{ width: matches ? '100%' : "45%", display: 'flex', justifyContent: 'center' }}>
                            <img src={ghf1} alt="" style={{ width: "80%", marginTop: '4rem' }} />
                        </Box>
                    </Box>
                </Container>
                <Box style={{ margin: `${match12 ? '5rem' : '-1rem'}  auto 0px auto`, width: 'fit-content' }}>
                    <Chavron />
                </Box>
                <br />
                <br />
                <Box style={{ ...arwimg, marginTop: '4rem' }}>
                    <Typography variant="h6" fontWeight={600} fontSize={match40 ? 25 : 40}>
                        Explore Our Services
                    </Typography>
                </Box>
             </Box>
            </div>
        </>
    );
}

export default HeroSection;
