import React,{useState} from 'react'
import Button from '@mui/material/Button'
import axios from 'axios'
import { useSnackbar } from "notistack";

const Login = () => {

  const { enqueueSnackbar } = useSnackbar();
    const[username, setUsername] = useState("")
    const[password, setPasswrod] = useState("")
    const loginadmin = async() =>{
        const login ={ username, password}
        try {
            const data = await axios.post("http://localhost:8000/api/admin/login",login)
            enqueueSnackbar("Admin Login seccsusful!", { variant: "success" });
            localStorage.setItem("admin", JSON.stringify(data.data))
            setUsername("")
            setPasswrod("")
            window.location.reload();
        } catch (error) {
            enqueueSnackbar(error.response.data, { variant: "error" });
            setUsername("")
            setPasswrod("")
        }
    }

  return (
    <>
     <div className="mainLogin">
       <form className='LoginForm'>
        <h3>Onliy Admin Can Login Here </h3>
       <input type="text" placeholder='username' value={username} onChange={(e)=>setUsername(e.target.value)}/> 
       <input type="password" placeholder='password' value={password} onChange={(e)=>setPasswrod(e.target.value)} /> 
       <Button variant="contained" onClick={loginadmin}>
         Login
       </Button>
       </form>    
    </div> 
    </>
  )
}

export default Login
