import React, { useContext, useEffect, useState } from "react";
import { Container, Box, Typography, Input, Modal, Button } from "@mui/material";
import { Adminstyle } from "../components/style";
import axios from 'axios'
import { format } from 'timeago.js';
import { useSnackbar } from "notistack";
import Loader from "../components/Loader";
import { useLocation } from "react-router-dom";
import { AppContext } from "../context/createContext";
import Login from "./Login";


const Admin = () => {

   const uselocation = useLocation()
   const{setPaths}=useContext(AppContext)
  const { enqueueSnackbar } = useSnackbar();
  const [getdata, setGetdata] = useState([])
  const [search, setSearch] = useState("");
  const[autantic ,setAuthantic] = useState(null)


  const reslt = getdata.filter((item) => {
    return (
      item?.name?.toLowerCase().includes(search.toLowerCase()) ||
      item?.email?.toLowerCase().includes(search.toLowerCase()) ||
      item?.phone?.toLowerCase().includes(search.toLowerCase()) ||
      item?.message?.toLowerCase().includes(search.toLowerCase())
    );
  });
  useEffect(()=>{
    setPaths(uselocation.pathname)
  },[uselocation])
  const [loading, setLoading] = useState(true)
  const [data, setData] = useState("");
  const [open, setOpen] = React.useState(false);
  const openModalFn = (sms) => {
    setData(sms);
    if (sms) {
      setOpen(true);
    }
  };
  const handleClose = () => setOpen(false);
  const fatchData = async () => {

    try {
      const data = await axios.get("http://localhost:8000/api/form/users")
      setGetdata(data.data)
      setLoading(false)
    } catch (error) {
      console.log(error);
    }
  }

  useEffect(() => {
    const admin =JSON.parse(localStorage.getItem("admin"))
     setAuthantic(admin)
    fatchData()
  }, [setAuthantic])

  const DeleteData = async (Id) => {
    setLoading(true)
    try {
      const data = await axios.post("http://localhost:8000/api/form/delete", Id)
      enqueueSnackbar(data.data.data, { variant: "success" });
      fatchData()
    } catch (error) {
      console.log(error)
    }
  }


  const logout = ()=>{
   localStorage.removeItem("admin")
   window.location.reload();
  }

  return (
  
    <div style={{ minHeight: '90vh' }}>
      {autantic === null ? <Login/>:null}
      {loading && <Loader />}
      <Container maxWidth="xl" style={{ margin: "6rem auto 2rem auto", overflow: 'auto' }}>
        <Box className="tabls">
          <Box
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              marginBottom: "2rem",

            }}>
            <Input
              type="text"
              placeholder="Search..."
              onChange={(e) => setSearch(e.target.value)} />
            <Typography variant="h6" fontWeight={600}>
               <Button variant="outlined" onClick={logout}>
                 Logout
               </Button>   Acme-Loans
            </Typography>
          </Box>
          {reslt?.length > 0 ? (<table>
            <thead>
              <tr>
                <th>Date-Time </th>
                <th>Name</th>
                <th>Email</th>
                <th>Phone</th>
                <th>message</th>
              </tr>
            </thead>
            <tbody>
              {reslt.reverse().map((user, i) => (
                <tr key={i}>
                  <td className="times" onClick={() => openModalFn(user)}>{format(user.createdAt)}</td>
                  <td onClick={() => openModalFn(user)}>{user.username}</td>
                  <td onClick={() => openModalFn(user)}>{user.email}</td>
                  <td onClick={() => openModalFn(user)}>{user.phone}</td>
                  <td onClick={() => openModalFn(user)}>{user.message.slice(0, 20)}...</td>
                  <td ><Button variant="contained" color="error" onClick={() => DeleteData(user)}>
                    delete
                  </Button></td>
                </tr>
              ))}
            </tbody>
          </table>) : <><p className="datanull">Do not have any User data</p></>}
        </Box>
        <div>
          <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description">
            <Box sx={Adminstyle}>
              <Typography id="modal-modal-title" variant="h6" component="h2">
                {data.username}
              </Typography>
              <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                {data.message}
                voluptates?
              </Typography>
            </Box>
          </Modal>
        </div>
      </Container>
    </div>
  );
};

export default Admin;
