import React, { useRef, useEffect, useContext, useState } from "react";
import HeroSection from "../components/HeroSection";
import Cards from "../components/Cards";
import Images from "../components/Images";
import Footer from "../components/Footer";
import { AppContext } from "../context/createContext";
import { useLocation } from "react-router-dom";


const Home = () => {



  const uselocation = useLocation();
  const { setPaths } = useContext(AppContext);
  const containerRef = useRef();
  const [isScrolling, setIsScrolling] = useState(false);

  const handleScroll = () => {
    const container = containerRef.current;
    const itemWidth = container.clientWidth;
    const currentIndex = Math.round(container.scrollLeft / itemWidth);
    const scrollTo = currentIndex * itemWidth;

    container.scrollTo({
      left: scrollTo,
      behavior: 'smooth',
    });
  };

  const handleWheel = (event) => {
    const container = containerRef.current;
    const delta = event.deltaY;

    if (!isScrolling) {
      setIsScrolling(true);

      const itemWidth = container.clientWidth;
      const currentIndex = Math.round(container.scrollLeft / itemWidth);
      const nextIndex = delta > 0 ? currentIndex + 1 : currentIndex - 1;
      const scrollTo = nextIndex * itemWidth;

      container.scrollTo({
        left: scrollTo,
        behavior: 'smooth',
      });

      setTimeout(() => {
        setIsScrolling(false);
      }, 500); // Adjust the delay as needed
    }
  };

  useEffect(() => {
    const container = containerRef.current;
    container.addEventListener('wheel', handleWheel);

    return () => {
      container.removeEventListener('wheel', handleWheel);
    };
  }, [isScrolling]);

  useEffect(() => {
    setPaths(uselocation.pathname);
  }, [uselocation]);

  return (
    <div
      className="snap-scroll-container"
      ref={containerRef}
      onScroll={handleScroll}
    >
      <div className="snap-scroll-item">
        <HeroSection />
      </div>
      <div className="snap-scroll-item">
        <Cards />
      </div>
      <div className="snap-scroll-item">
        <Images />
      </div>
      <div className="snap-scroll-item">
        <Footer />
      </div>
    </div>
  );
};

export default Home;
