import React, { useContext, useState, useEffect } from "react";
import { AppContext } from "../context/createContext";
import { ResponsiveContainer, LineChart, XAxis, YAxis, Tooltip, Line } from "recharts";
import Button from '@mui/material/Button'
import { Link } from "react-router-dom";
import useMediaQuery from "@mui/material/useMediaQuery";



const CustomTooltip = ({ active, payload, label, interestRate }) => {
  let remainingLoan = 'N/A';

  if (active && payload && payload.length > 0) {
    // if (interestOnly > 0) {
    //   remainingLoan = payload[1] && payload[1].value !== undefined ? payload[1].value : 'N/A';
    // }
    remainingLoan = payload[0] && payload[0].value !== undefined ? payload[0].value : 'N/A';
    //  else {}
  }

  if (active) {

    return (
      <div className="custom-tooltip">
        <p>{`Principal Remaining: ${remainingLoan.toLocaleString('en-US', {
          style: 'currency',
          currency: 'USD',
        })
          .replace(/\.00$/, '')}`}</p>
        <p className="label">{`Years Remaining: ${label}`}</p>
        <p>{`Rate: ${interestRate}%`}</p>
      </div>
    );
  }
  return null;
};

const ChartCpnt = () => {
  const [showData, setChartData] = useState([]);
  const { setData } = useContext(AppContext)
  const { totleYears, loanAmount, interestOnly, interestRate, totleLoan, interestchrg, secsuse } = useContext(AppContext);
  const match50 = useMediaQuery("(max-width:500px)");
  useEffect(() => {

    let calutyears = totleYears

    if (calutyears <= 7) {
      if (interestOnly === 1) {
        calutyears -= 1
      } else if (interestOnly === 2) {
        calutyears -= 2
      } else if (interestOnly === 3) {
        calutyears -= 3
      }
    } else {
      if (interestOnly === 2) {
        calutyears -= 1
      } else if (interestOnly === 3) {
        calutyears -= 2
      }
    }
    const calculatedYears = Array.from({ length: totleYears }, (_, i) => i + 1);
    const yearsToSkip = interestOnly > 0 ? interestOnly : 0;
    const graphyears = (loanAmount / calutyears).toFixed(0)
    const xAxisCategories = [];
    for (let i = 1; i <= totleYears; i++) {
      xAxisCategories.push(`${i}`);
    }
    let updatedLoanAmount = loanAmount;
    const updatedLoanAmounts = calculatedYears.map((year) => {

      if (totleYears >= 7) {
        if (year === 1) {
          return updatedLoanAmount;
        }
      }
      if (year <= yearsToSkip) {
        return updatedLoanAmount;
      }
      updatedLoanAmount -= graphyears;

      return updatedLoanAmount;
    });
    xAxisCategories.reverse();
    const chartData = xAxisCategories.map((name, index) => ({
      name,
      interestOnly: index < interestOnly ? loanAmount : 0,
      uv: index === updatedLoanAmounts.length - 1 && updatedLoanAmounts[index] === -1 ? 0 : updatedLoanAmounts[index],
    }));
    setChartData(chartData);

  }, [loanAmount, totleYears, interestOnly]);

  return (
    <div className="maindiv">
      <div>
        {secsuse}
      </div>
      <p style={{ paddingLeft: '0rem', fontWeight: '600' }}>Loan Balance Remaining {loanAmount !== null ? loanAmount.toLocaleString('en-US', {
        style: 'currency',
        currency: 'USD',
      }).replace(/\.00$/, '') : 'N/A'}</p>

      <ResponsiveContainer width="100%" height={400} className="chart">
        <LineChart data={showData}>
          <XAxis dataKey="name" domain={[0, "dataMax"]} type="category" />
          <YAxis />
          <Tooltip content={<CustomTooltip totleYears={totleYears} interestRate={interestRate} interestOnly={interestOnly} showData={showData} />} />
          {interestOnly > 0 ? (
            <>
              {/* <Area dataKey="interestOnly" fill="#5c5cff" /> */}
              <Line type="monotone" dataKey="uv" stroke="#8884d8" strokeWidth={3} />
            </>
          ) : (
            <Line type="monotone" dataKey="uv" stroke="#8884d8" strokeWidth={3} />
          )}

        </LineChart>
      </ResponsiveContainer>
      <div className="legend">
        <div className="legend-label">Today</div>
      <Link to="/contact"  style={{display:match50 ? 'none':'block'}}><Button variant="contained" style={{background:'#5c5cff',textTransform:'capitalize'}} onClick={()=>setData("Speak to an advisor ")}>Speak to an advisor </Button></Link>
        <div className="legend-label">{totleYears} Years</div>
      </div>
      <div className="showdata">
        <div>
        <Link to="/contact" style={{display:match50 ? 'block':'none'}}><Button variant="contained" style={{background:'#5c5cff',textTransform:'capitalize'}} onClick={()=>setData("Speak to an advisor ")}>Speak to an advisor </Button></Link> <br />
          Total loan repayments <br /><h3 className="amountresutl"> {totleLoan && totleLoan.toLocaleString('en-US', {
          style: 'currency',
          currency: 'USD',
        }).replace(/\.00$/, '')}</h3></div>
        <div>Total interest charged <br /><h3 className="amountresutl"> {interestchrg && interestchrg.toLocaleString('en-US', {
          style: 'currency',
          currency: 'USD',
        }).replace(/\.00$/, '')}</h3></div>
      </div>
    </div>

  );
};

export default ChartCpnt;
