import React, { useState, useEffect } from "react";
import { Box, Button } from "@mui/material";
import {
  calculaterdiv,
  inputscul,
  calbtndiv,
  seconculdiv,
  heroBTN,
  bckalbtn,
} from "../components/style";
import useMediaQuery from "@mui/material/useMediaQuery";
import "apexcharts/dist/apexcharts.css";
import { useContext } from "react";
import { AppContext } from "../context/createContext";
import { Link ,useLocation} from 'react-router-dom'
import ChartCpnt from "../components/ChartCpnt";
import { useSnackbar } from "notistack";
import Loader from "../components/Loader";


const Calculators = () => {

   const uselocation = useLocation()
  const [loading, setLoading] = useState(false)
  const { enqueueSnackbar } = useSnackbar();
  const matches = useMediaQuery("(max-width:900px)");
  const match50 = useMediaQuery("(max-width:500px)");
  const [loanAmount, setLoanAmount] = useState("");
  const [term, setTerm] = useState("Select Years");
  const [interestRate, setInterestRate] = useState("");
  const [interestOnlyYears, setInterestOnlyYears] = useState(0);
  const [activs, setActivs] = useState(false)
  const {
    setTotleYears,
    setTotleLoan,
    setInterestchrg,
    setInterestRates,
    setLoanAmounts,
    setInterestOnly,
    setSecsuse,
    setData,
    setPaths
  } = useContext(AppContext)


  useEffect(()=>{
    setData('')
    setPaths(uselocation.pathname)
  },[uselocation])

  const calculateMonthlyRepayment = () => {
    setLoading(true)
    if (loanAmount && interestRate && !isNaN(term)) {
      setActivs(true);
    } else {
      setActivs(false);
      setLoading(false)
      enqueueSnackbar("Please fill in all the fields.", { variant: "error" });
    }

    let loanAmountValue = parseFloat(loanAmount);
    const interestRateValue = parseFloat(interestRate);
    const termValue = parseInt(term);
    const interestOnlyYearsValue = interestOnlyYears;
    const monthlyInterestRate = interestRateValue / 100 / 12;
    const numberOfPayments = termValue * 12;
    setLoanAmounts(loanAmountValue)
    setTotleYears(termValue)
    setInterestRates(interestRateValue)
    setInterestOnly(interestOnlyYearsValue)
    let monthlyPayment;
    let totalAmount;
    let totalInterest;
    let totalLoanAmountWithInterest;

    if (interestOnlyYearsValue > 0) {
      const interestOnlyMonths = interestOnlyYearsValue * 12;
      const interestOnlyPayments = loanAmountValue * monthlyInterestRate * interestOnlyMonths;
      const remainingMonths = numberOfPayments - interestOnlyMonths;

      monthlyPayment = (loanAmountValue * monthlyInterestRate * Math.pow(1 + monthlyInterestRate, remainingMonths)) / (Math.pow(1 + monthlyInterestRate, remainingMonths) - 1);

      totalAmount = interestOnlyPayments + Math.ceil(monthlyPayment * remainingMonths);
      totalInterest = totalAmount - loanAmountValue;
      totalLoanAmountWithInterest = Math.ceil(totalAmount);

      let interestOnlyMonthly = Math.ceil(interestOnlyPayments / interestOnlyMonths);
      setSecsuse(
        <div>
          <p>
            Your approximate interest-only repayments would be <span className="months">
              {interestOnlyMonthly
                .toLocaleString('en-US', {
                  style: 'currency',
                  currency: 'USD',
                })
                .replace(/\.00$/, '')}
            </span>  per month
          </p>
          <p>
            Once the interest-only period ends, your repayments will be <span className="months">
              {Math.ceil(totalAmount / numberOfPayments)
                .toLocaleString('en-US', {
                  style: 'currency',
                  currency: 'USD',
                })
                .replace(/\.00$/, '')}</span> per month
          </p>
        </div>
      );
      setTotleLoan(totalLoanAmountWithInterest)
      setInterestchrg(Math.ceil(totalInterest))

    } else {
      monthlyPayment = (loanAmountValue * monthlyInterestRate * Math.pow(1 + monthlyInterestRate, numberOfPayments)) / (Math.pow(1 + monthlyInterestRate, numberOfPayments) - 1);
      totalAmount = Math.ceil(monthlyPayment * numberOfPayments);
      totalInterest = totalAmount - loanAmountValue;
      totalLoanAmountWithInterest = Math.ceil(totalAmount);
      setTotleLoan(totalLoanAmountWithInterest)
      setInterestchrg(Math.ceil(totalInterest))

      setSecsuse(
        <div>
          <p >Your approximate repayments will be <span className="months">
            {Math.ceil(totalAmount / numberOfPayments).toLocaleString('en-US', {
              style: 'currency',
              currency: 'USD',
            }).replace(/\.00$/, '')}
          </span> per month
          </p>
        </div>
      );

    }
    setLoading(false) 
  };




  return (
    <>
  {loading && <Loader/>}
      <div style={{ ...calculaterdiv, marginTop: "3rem" }}>
        <Box style={{ width: matches ? "90%" : "900px" }}>
          <h1 style={{ textAlign: "center" }}>Loan Repayment Calculator</h1>
          <Box className="calculator_div">
            <Box
              style={{
                ...seconculdiv,
                flexDirection: match50 ? "column" : "row",
              }}>
              <Box width={"100%"}>
                <label htmlFor="1"> Loan Amount ($)</label>
                <br />
                <input
                  type="number"
                  placeholder="Enter Total Loan Amount"
                  id="1"
                  style={inputscul}
                  value={loanAmount}
                  onChange={(e) => setLoanAmount(e.target.value)}
                />
              </Box>
              <Box width={"100%"}>
                <label htmlFor="2">Term</label>
                <br />
                <select
                  style={inputscul}
                  value={term}
                  onChange={(e) => setTerm(e.target.value)}
                  
                  >
                  <option value="Select Years">Select Years</option>
                  {[...Array(30).keys()].map((item, i) => (
                    <option key={i} value={item + 1}>
                      {item + 1} {item === 0 ? 'Year' : 'Years'}
                    </option>
                  ))}
                </select>


              </Box>
            </Box>
            <Box
              style={{
                ...seconculdiv,
                flexDirection: match50 ? "column" : "row",
              }}>
              <Box width={"100%"}>
                <label htmlFor="3">Repayment Type</label>
                <br />
                <select
                  style={inputscul}
                  value={interestOnlyYears}
                  onChange={(e) => setInterestOnlyYears(parseInt(e.target.value))}>
                  <option value="0">Principal and interest</option>
                  {[...Array(3).keys()].map((item, i) => (
                    <option key={i} value={(item + 1).toString()}> {/* Convert to string */}
                      {item === 0 ? 'Interest only 1 year' : `Interest only ${item + 1} years`}
                    </option>
                  ))}
                </select>


              </Box>
              <Box width={"100%"}>
                <label htmlFor="4">Interest rate (%)</label>
                <br />
                <input
                  type="number"
                  id="4"
                  placeholder="Enter Rate"
                  style={inputscul}
                  value={interestRate}
                  onChange={(e) => setInterestRate(e.target.value)} />
              </Box>
            </Box>
            <Box style={calbtndiv}>
              <Button
                variant="contained"
                className="btncontect"
                style={heroBTN}
                onClick={calculateMonthlyRepayment}>
                Calculate
              </Button>
              <Link to='/Contact' onClick={() => setData("Book A Call")}> <Button
                variant="outlined"
                className="btncontect"
                style={bckalbtn}>
                Book a Call
              </Button></Link>
            </Box>
          </Box>
          {loanAmount && interestRate && term && activs && <ChartCpnt />}
        </Box>
      </div>
    </>
  );
};

export default Calculators;