import React,{useRef,useEffect} from 'react'
import Typography from '@mui/material/Typography'
import { footerdiv, fotcontdiv, fotplcy } from './style'
import { Container, Box } from '@mui/material'
import useMediaQuery from '@mui/material/useMediaQuery';
import logo from '../assets/flogo.png'
import { Link ,useLocation } from 'react-router-dom';
import imgH from '../assets/home.png'


const Footer = () => {

  const matches = useMediaQuery('(max-width:500px)');
  const match70 = useMediaQuery('(max-width:700px)');
  const location = useLocation()
  const contextRef = useRef(null);


   useEffect(() => {
   
     if(!match70){
      const observer = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.style.transition = "opacity 2s, transform 2s"; // Adjust the duration as needed
            entry.target.style.opacity = 1;
            entry.target.style.transform = "translateY(0)";
          } else {
            entry.target.style.transition = "opacity 2s, transform 2s"; // Adjust the duration as needed
            entry.target.style.opacity = 0;
            entry.target.style.transform = "translateY(20%)";
          }
        });
      });
  
      const cards = contextRef.current.querySelectorAll(".anima");
      cards.forEach((card) => {
        observer.observe(card);
      });
      return () => {
        observer.disconnect();
      };
     }
   }, [match70]);

  return (
    <>
      <div style={{ background: '#05071e' }} className={match70 ? null:location.pathname === '/'?'scroldiv':''} ref={contextRef}>
        <div className={ match70 ? '':location.pathname === '/'? 'anima':''}>
          <Container maxWidth="xl" style={{ ...footerdiv, padding: matches ? '2rem 10px' : '2rem', }}>
            <Box style={{ ...fotcontdiv, textAlign: match70 ? 'center' : 'unset' }}>
              <Box style={{ ...fotplcy, flexDirection: match70 ? 'column' : 'row', textAlign: match70 ? 'center' : 'unset' }}>
                <Link to='/'>
                  <img src={logo} alt="" width={200} />
                </Link>
                <Typography variant="body1">
                  <img src={imgH} alt="" /> &nbsp; 470 St Kilda Road, Melbourne VIC 3004
                </Typography>
                <Link to='TermsandConditions/' className='termlnk linktag'>
                  Terms and Conditions
                </Link>
                <Link to='/PrivacyPolicy' className='termlnk linktag'>
                  Privacy Policy
                </Link>
              </Box>
              <Typography variant="body1" style={{ width: '100%', textAlign: 'center', fontSize: '14px' }}>
                Acme Loans Pty Ltd (ABN 43 670 222 250 / Credit Representative Number 551870) has access to a panel of lenders through National Mortgage Brokers Pty Ltd., (ACN 093 874 376 / Australian Credit Licence 391209), which is a fully-owned subsidiary of Liberty Financial Pty Ltd (ACN 077 248 983 / Australian Credit Licence 286596). Acme Loans Pty Ltd has access to products including those from Liberty Financial.
              </Typography>
            </Box>
          </Container>
          <Typography variant="h6" className='copyrights'> 2023 Acme Loans Pty Ltd. ABN 43 670 222 250. All rights reserved </Typography>
        </div>
      </div>
    </>
  )
}

export default Footer
